import blRight from '@/components/bl_right'
import mainLogo from '@/components/main_logo'
import copyright from '@/components/copyright'
import pageHeader from '@/components/page_header'
import Glide from '@glidejs/glide'
import clipboardJS from 'clipboard'
import Inputmask from 'inputmask'
import ymaps from 'ymaps'
import axios from 'axios'

export default {
	head() {
		return {
			title: 'Systemice Hotel Group /   ',
			script:[],
			meta: [
			{
				hid: 'name',
				name: 'name',
				content: 'Systemice Hotel Group'
			},
			{
				hid: 'description',
				name: 'description',
				content: '       '
			},
            {
                hid: 'og:title',
                name: 'og:title',
                content: 'Systemice Hotel Group /   ',
            },
            {
                hid: 'og:image',
                property: 'og:image',
                content: 'https://systemice.ru/images/systemice.jpg',
            },
            {
            	hid: 'og:image:width',
                property: 'og:image:width',
                content: '400',
            },
            {
            	hid: 'og:image:height',
                property: 'og:image:height',
                content: '300',
            },
            {
            	hid: 'og:image:alt',
                property: 'og:image:alt',
                content: 'Systemice.ru - Systemice Hotel Group /   ',
            },
            {
                hid: 'og:description',
                property: 'og:description',
                content: '       ',
            },
            {
                hid: 'og:url',
                property: 'og:url',
                content: `https://systemice.ru`,
            },
			{
				hid: 'itemprop:name',
				itemprop: "name",
				content: "Systemice Hotel Group /   "
			},
			{
				hid: 'itemprop:description',
				itemprop: "description",
				content: "       "
			},
			{
				hid: 'itemprop:image',
				itemprop: "image",
				content: "//systemice.ru/images/systemice.jpg"
			},
			{
				hid: 'twitter:card',
				name: "twitter:card",
				content: "summary_large_image"
			},
			{
				hid: 'twitter:title',
				name: "twitter:title",
				content: "Systemice Hotel Group /   "
			},
			{
				hid: 'twitter:description',
				name: "twitter:description",
				content: "       "
			},
			{
				hid: 'twitter:image',
				name: "twitter:image",
				content: "//systemice.ru/images/systemice.jpg"
			}
			]
		}
	},
	data(){
		return {
			portfolioData: this.$store.state.portfolioData,
			name: '',
			phone: '',
			agreeTerms: 'yes',
			im: new Inputmask("+7 (999) 999-99-99"),
			sendingForm: 0,
			glide: {},
			glide_a: {},
			offices: {
				'sankt-peterburg': '. -,   ., 68, . 1',
				'sochi': '',
				'krasnodar': '. , . , . 32',
				'moscow': '. ,  . ,  35,  64,  8  ',
				'tula': '. , . , . 38',
				'rostov': '. --,  , 82/4'
			}
		}
	},
	components: {
		blRight, pageHeader, mainLogo, copyright
	},

	methods: {
		moveRight(){

			this.glide.go('>')
		},
		moveLeft(){

			this.glide.go('<')
		},

		startVideo(event){

			event.target.style.backgroundImage = 'none'
			event.target.querySelector('iframe').style.visibility = "visible"
			event.target.querySelector('iframe').setAttribute('src', event.target.querySelector('iframe').getAttribute('data-src'))
		},
		checkForm(e){

			e.preventDefault()

			if (this.agreeTerms == 'no' || !e.target.querySelector('.contact-phone').inputmask.isComplete()) {

				if (this.agreeTerms == 'no') {
					document.querySelector('.i-agree').style = "color: red;"
				}
				if (!e.target.querySelector('.contact-phone').inputmask.isComplete()) {
					document.querySelector('.contact-phone').classList.add('input-box-wrong')
				}
				return false
			}

			this.sendForm(e.target)
		},

		sendForm(form){

			if (this.sendingForm != 0) return false

				this.sendingForm = 1

			axios.interceptors.request.use((req) => {
				form.querySelector('.send-button').value = "..."
				return req
			}
			)

			let bodyFormData = new FormData()
			bodyFormData.append('name', form.querySelector('.contact-name').value)
			bodyFormData.append('phone', form.querySelector('.contact-phone').value)
			bodyFormData.append('form_name', form.getAttribute('name'))
			bodyFormData.append('u', document.body.getAttribute('data-u'))
			bodyFormData.append('dopinfo', ',   (systemice.ru)')
			bodyFormData.append('hotel', 13632)
			bodyFormData.append('form_name_text', ',   (systemice.ru)')

			axios.post('https://systemice.ru/knight_bron.php', bodyFormData, {
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				}
			})
			.then(response => {

				if (!response.data || response.data == '') {
					form.querySelector('.send-button').value = "!"
					return false
				}

				form.querySelector('.send-button').value = "!"

				this.$metrika.reachGoal('contacts_send')
			})
		},
		createForm() {

			this.$metrika.reachGoal('callback_0')

			document.querySelector('body').insertAdjacentHTML('beforeend', `
				<div class = "overlay">
					<div class = "fadein-form">
						<form name = "fadein_form">
							<div class = "border-callback border-callback-left"></div>
							<div class = "border-callback border-callback-top"></div>
							<div class = "border-callback border-callback-right"></div>
							<div class = "border-callback border-callback-bottom"></div>
							<p class = "fadein-title">  </p>
							<div class="fadein-box">
								<input type="text" name="name" class = "fadein-input fadein-name" placeholder=" ">
								<div class="spy-left-input"></div>
								<div class="spy-top-input"></div>
								<div class="spy-right-input"></div>
								<div class="spy-bottom-input"></div>
							</div>
							<div class="fadein-box">
								<input type="text" name="phone" class = "fadein-input fadein-phone" placeholder="">
								<div class="spy-left-input"></div>
								<div class="spy-top-input"></div>
								<div class="spy-right-input"></div>
								<div class="spy-bottom-input"></div>
							</div>
							<div class="fadein-box">
								<input type="text" name="email" class = "fadein-input fadein-email" placeholder="Email">
								<div class="spy-left-input"></div>
								<div class="spy-top-input"></div>
								<div class="spy-right-input"></div>
								<div class="spy-bottom-input"></div>
							</div>

							<div class="fadein-box fadein-box-submit">
								<input type="submit" name="submit" class = "fadein-input fadein-submit" placeholder="">
								<div class="spy-left-input"></div>
								<div class="spy-top-input"></div>
								<div class="spy-right-input"></div>
								<div class="spy-bottom-input"></div>
							</div>
							<img src = "/images/icons/close-white.svg" class = "fadein-close">
						</form>
					</div>
				</div>`)

			this.im.mask(document.querySelector(".fadein-phone"))

			document.querySelector('.fadein-submit').addEventListener('click', e => {
				this.checkFadeInForm(e, document.querySelector('.fadein-form'))
			})

			document.querySelector('.fadein-close').addEventListener('click', () => {
				this.closeForm()
			})

			document.querySelector(".fadein-phone").addEventListener('keyup', function() {
				this.classList.remove('input-box-wrong')
			})

			document.querySelector('.overlay').addEventListener('click', e => {

				if (e.target.classList.contains('overlay')) this.closeForm()
			})

			this.body_lock()
		},
		closeForm(){

			document.querySelector('.overlay').remove()
			this.body_unlock()
		},
		checkFadeInForm(e, form){

			e.preventDefault()

			if (!form.querySelector('.fadein-phone').inputmask.isComplete()) {

				form.querySelector('.fadein-phone').classList.add('input-box-wrong')
				return false
			}

			this.sendFadeInForm(form)
		},
		sendFadeInForm(form){

			if (this.sendingForm != 0) return false

				this.sendingForm = 1

			axios.interceptors.request.use((req) => {
				form.querySelector('.fadein-submit').value = "..."
				return req
			}
			)

			let bodyFormData = new FormData()
				bodyFormData.append('name', form.querySelector('.fadein-name').value)
				bodyFormData.append('phone', form.querySelector('.fadein-phone').value)
				bodyFormData.append('form_name', form.querySelector('form').getAttribute('name'))
				bodyFormData.append('email', form.querySelector('.fadein-email').value)
				bodyFormData.append('u', document.body.getAttribute('data-u'))
				bodyFormData.append('dopinfo', '  ')
				bodyFormData.append('hotel', 13632)
				bodyFormData.append('form_name_text', '  ')

			axios.post('https://systemice.ru/knight_bron.php', bodyFormData, {
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				}
			})
			.then(response => {

				this.sendingForm = 0

				if (!response.data || response.data == '') {
					form.querySelector('.fadein-submit').value = "!"
					return false
				}

				form.querySelector('.fadein-submit').value = "!"

				this.$metrika.reachGoal('callback_1')
			})
		},
		body_lock() {
			let body = document.body;
			if (!body.classList.contains('scroll-locked')) {
				let bodyScrollTop = (typeof window.pageYOffset !== 'undefined') ? window.pageYOffset : (document.documentElement || document.body.parentNode || document.body).scrollTop;
				body.classList.add('scroll-locked');
				body.style.top = "-" + bodyScrollTop + "px";
				body.setAttribute("data-popup-scrolltop", bodyScrollTop)
			}
		},
		body_unlock() {
			let body = document.body;
			if (body.classList.contains('scroll-locked')) {
				let bodyScrollTop = document.body.getAttribute("data-popup-scrolltop");
				body.classList.remove('scroll-locked');
				body.style.top = "";
				body.removeAttribute("data-popup-scrolltop")
				window.scrollTo(0, bodyScrollTop)
		}
}
	},
	mounted(){

		/*  */

		if (document.getElementById('copy-text')) {
			let clipboard = new clipboardJS('#copy-text');

			clipboard.on('success', function(e) {
				document.getElementById('copy-text').innerText = '';
				setTimeout(function(){
					document.getElementById('copy-text').innerText = '   ';
				}, 1000)
			});
		}

		/* ,  */

			//  glide js     

			this.glide_a = new Glide('.glide-a', {
				type: 'carousel',
				startAt: 1,
				perView: 1,
				gap: 5,
				breakpoints: {
					800: {
						perView: 1
					},
					480: {
						perView: 1,
						gap: 20
					}
				}
			})
			
			if (window.screen.width < 1101) {
				this.glide_a.mount()
			}

			//  

			this.glide = new Glide('.glide', {
				type: 'carousel',
				startAt: 0,
				perView: 4,
				gap: 0,
				breakpoints: {
					1200: {
						perView: 2
					},
					768: {
						perView: 1
					}
				}
			})

			let glide = this.glide

			glide.mount()


		//    

		let phones = document.getElementsByClassName("contact-phone");

		for (let i = 0; i < phones.length; i++) {
			this.im.mask(phones[i]);
		}

		for (let i = 0; i < phones.length; i++) {
			phones[i].addEventListener('keyup', function(){
				this.classList.remove('input-box-wrong');
			})
			phones[i].addEventListener('focus', function(){
				this.classList.remove('input-box-wrong');
			})
		}


		let label = document.getElementsByClassName('checkbox-block');
		for (let i = 0; i < label.length; i++) {
			label[i].addEventListener('click', function(){
				label[i].querySelector('.i-agree').style = "color: white;"
			})
		}

		/*  */

		let myMap, myPlacemark,
		koordinats = [55.776108388031815, 37.67544507980347],
		companyName = 'Systemice Hotel Group',
		balloonContent = '.  .62.<br>8 . 804 '

		ymaps.load('https://api-maps.yandex.ru/2.1/?lang=ru_RU')
		.then(maps => {

			myMap = new maps.Map('map-block', {
				center: koordinats,
				zoom: 12
			})
			
			myPlacemark = new maps.Placemark(koordinats, {
				hintContent: 'Systemice Hotel Group',
				balloonContent: '.  .62. -  . 8 . 804 '
			})

			myMap.geoObjects.add(myPlacemark)
			myMap.setType('yandex#map')
			myMap.behaviors.disable('scrollZoom')
		})
		.catch(error => console.log('Failed to load Yandex Maps', error))

		/* ,  */

		//     
	}
}
